<template>
    <div class="mymoney">
      <mt-header title="我的余额" id="header">
        <router-link to="/" slot="left" >
          <mt-button icon="back"></mt-button>
        </router-link>
      </mt-header>
      <div class="cont">
        <p>$1000000.00</p>
      </div>
      <mt-button type="primary" class="btn" @click="tan">申请提现</mt-button>
    </div>
</template>

<script>
    export default {
        name: "mymoney",
      methods:{
          tan(){
            alert('老铁，提现成功！')
          }
      }
    }
</script>

<style scoped>
.mymoney .cont{
  width: 330px;
  height: 150px;
  background: #fff;
  text-align: center;
  margin: 15px auto;
  line-height: 150px;
}
.mymoney .cont p{
  color: red;
  font-size: 30px;
  font-weight: 700;
}
.mymoney  .btn{
  width: 90%;
  margin: 30px auto;
  margin-left: 20px;
}
</style>



